                                "description": "火锅食材贝类 生鲜",
                                "icon": "https://m.360buyimg.com/mobilecms/s750x750_jfs/t1/171918/6/4940/300074/607aa8ebEf9702ba0/6cff9c166521d87c.jpg!q80.dpg.webp"
