                                "icon": "https://m.360buyimg.com/mobilecms/s750x750_jfs/t1/167700/8/18748/219167/60799855E870b4a6a/ab6d827a71da2e21.jpg!q80.dpg.webp"
                            },
                            {
                                "quantity": 0,
                                "id": 17,
                                "name": "原味盐焗坚果干果",
                                "price": 13.9,
                                "stock": 105,
                                "description": "盐焗开心果1罐装",
                                "icon": "https://m.360buyimg.com/mobilecms/s750x750_jfs/t1/103668/15/17528/180008/5e86f0d5E4388cccf/1cd7feb9bb083a06.jpg!q80.dpg.webp"
                            }
                        ]
                    }
                ]
            }
        },
        created(){
            this.$store.state.index = 1;
            this.$store.state.selectedArray = [];
            const _this = this
            axios.get(this.$store.state.globalhost+'product-service/buyer/product/list').then(function (resp) {
                _this.data = resp.data.data
            })
        },
        methods: {
            //提交
            submit(){
                const _this = this
                if(this.$store.state.selectedArray.length == 0) {
                    let instance = Toast('请选择商品');
                    setTimeout(() => {
                        instance.close();
                    }, 1000)
                    return
                }
                this.$router.push('/info')
            },
            //清空购物车
            clear(){
                this.totalQuantity=0;
                this.totalPrice=0;
                //菜品的数量设置为0
                for(var i = 0; i < this.data.length;i++){
                    var item = this.data[i];
                    for(var j=0;j<item.foods.length;j++){
                        item.foods[j].quantity = 0;
                    }
                }
                //清空已选数组
                this.$store.state.selectedArray=[]
            },
            changeQuantity(item,type){
                switch (type) {
                    case "minus":
                        if(item.quantity == 0)return;
                        item.quantity--;
                        this.totalQuantity--;
                        this.totalPrice -= item.price;
                        for(var i=0;i<this.$store.state.selectedArray.length;i++){
                            if(item.id == this.$store.state.selectedArray[i].productId){
                                this.$store.state.selectedArray[i].productQuantity = item.quantity
                                if(item.quantity==0) this.$store.state.selectedArray.splice(i,1)
                                return;
                            }
                        }
                        break;
                    case "plus":
                        item.quantity++;
                        if(item.quantity <= item.stock){
                            this.totalQuantity++;
                            this.totalPrice += item.price;
                        }else{
                            item.quantity = item.stock;
                            let instance = Toast(item.name+'已被你抢空！');
                            setTimeout(() => {
                                instance.close();
                            }, 1000)
                            return
                        }
                        //查询是否已经存在
                        for(var i=0;i<this.$store.state.selectedArray.length;i++){
                            if(item.id == this.$store.state.selectedArray[i].productId){
                                this.$store.state.selectedArray[i].productQuantity = item.quantity
                                return;
                            }
                        }
                        //添加对象
                        this.$store.state.selectedArray.push({
                            productId:item.id,
                            productQuantity:item.quantity
                        });
                        break;
                }
            }
        }
    }
</script>

